/**
 * 
 * 
 */
export class ClipboardUtil {
    /**
     * 
     * @param {string} text - 
     * @returns {Promise<boolean>} 
     */
    static async copyText(text) {
        try {
            // 
            if (typeof text !== 'string') {
                throw new Error('')
            }

            if (text.trim() === '') {
                throw new Error('')
            }

            //  Clipboard API
            if (navigator.clipboard && window.isSecureContext) {
                await navigator.clipboard.writeText(text)
                return true
            } else {
                // 
                return this.fallbackCopyText(text)
            }
        } catch (error) {
            console.error(':', error)
            return false
        }
    }

    /**
     * 
     * @param {string} text - 
     * @returns {boolean} 
     */
    static fallbackCopyText(text) {
        let input = null
        try {
            // 
            input = document.createElement('textarea')
            input.value = text

            // 
            input.style.position = 'fixed'
            input.style.left = '-9999px'
            input.style.top = '-9999px'
            input.style.opacity = '0'
            input.style.pointerEvents = 'none'

            document.body.appendChild(input)

            // 
            input.focus()
            input.select()
            input.setSelectionRange(0, text.length)

            // 
            const success = document.execCommand('copy')
            return success
        } catch (error) {
            console.error(':', error)
            return false
        } finally {
            // 
            if (input && input.parentNode) {
                document.body.removeChild(input)
            }
        }
    }

    /**
     * 
     * @param {string|HTMLCanvasElement|Blob} source - 
     * @returns {Promise<boolean>} 
     */
    static async copyImage(source) {
        try {
            if (!navigator.clipboard || !navigator.clipboard.write) {
                throw new Error('')
            }

            let blob

            if (source instanceof Blob) {
                blob = source
            } else if (source instanceof HTMLCanvasElement) {
                blob = await this.canvasToBlob(source)
            } else if (typeof source === 'string') {
                // DataURLURL
                blob = await this.dataURLToBlob(source)
            } else {
                throw new Error('')
            }

            const item = new ClipboardItem({ [blob.type]: blob })
            await navigator.clipboard.write([item])
            return true
        } catch (error) {
            console.error(':', error)
            return false
        }
    }

    /**
     * CanvasBlob
     * @param {HTMLCanvasElement} canvas - Canvas
     * @param {string} type - 
     * @param {number} quality - 
     * @returns {Promise<Blob>} Blob
     */
    static canvasToBlob(canvas, type = 'image/png', quality = 1.0) {
        return new Promise((resolve) => {
            canvas.toBlob(resolve, type, quality)
        })
    }

    /**
     * DataURLBlob
     * @param {string} dataURL - DataURL
     * @returns {Promise<Blob>} Blob
     */
    static async dataURLToBlob(dataURL) {
        try {
            const response = await fetch(dataURL)
            return await response.blob()
        } catch (error) {
            console.error('DataURLBlob:', error)
            throw error
        }
    }

    /**
     * 
     * @returns {Promise<string>} 
     */
    static async readText() {
        try {
            if (navigator.clipboard && navigator.clipboard.readText) {
                return await navigator.clipboard.readText()
            } else {
                throw new Error('')
            }
        } catch (error) {
            console.error(':', error)
            throw error
        }
    }

    /**
     * 
     * @returns {Promise<Blob>} Blob
     */
    static async readImage() {
        try {
            if (!navigator.clipboard || !navigator.clipboard.read) {
                throw new Error('')
            }

            const clipboardItems = await navigator.clipboard.read()
            
            for (const clipboardItem of clipboardItems) {
                for (const type of clipboardItem.types) {
                    if (type.startsWith('image/')) {
                        return await clipboardItem.getType(type)
                    }
                }
            }
            
            throw new Error('')
        } catch (error) {
            console.error(':', error)
            throw error
        }
    }

    /**
     * API
     * @returns {Object} 
     */
    static checkSupport() {
        return {
            writeText: !!(navigator.clipboard && navigator.clipboard.writeText),
            readText: !!(navigator.clipboard && navigator.clipboard.readText),
            writeImage: !!(navigator.clipboard && navigator.clipboard.write),
            readImage: !!(navigator.clipboard && navigator.clipboard.read),
            isSecureContext: window.isSecureContext
        }
    }

    /**
     * 
     * @param {Array} lines - 
     * @returns {Promise<boolean>} 
     */
    static async copyLines(lines) {
        const text = Array.isArray(lines) ? lines.join('\n') : String(lines)
        return await this.copyText(text)
    }

    /**
     * JSON
     * @param {Object} obj - 
     * @param {number} indent - 
     * @returns {Promise<boolean>} 
     */
    static async copyJSON(obj, indent = 2) {
        try {
            const jsonText = JSON.stringify(obj, null, indent)
            return await this.copyText(jsonText)
        } catch (error) {
            console.error('JSON:', error)
            return false
        }
    }

    /**
     * 
     * @param {Array} data - 
     * @param {Array} headers - 
     * @returns {Promise<boolean>} 
     */
    static async copyTable(data, headers = null) {
        try {
            let text = ''
            
            // 
            if (headers && headers.length > 0) {
                text += headers.join('\t') + '\n'
            }
            
            // 
            data.forEach(row => {
                if (Array.isArray(row)) {
                    text += row.join('\t') + '\n'
                } else if (typeof row === 'object') {
                    const values = headers ? headers.map(header => row[header] || '') : Object.values(row)
                    text += values.join('\t') + '\n'
                }
            })
            
            return await this.copyText(text.trim())
        } catch (error) {
            console.error(':', error)
            return false
        }
    }
}

export default ClipboardUtil
