<template>
  <div class="main-container">
    <div class="tool-header">
      <h1> /</h1>
      <p></p>
    </div>

    <el-card class="tool-card">
      <div slot="header">
        <span>/</span>
      </div>
      
      <el-form :model="form" label-width="70px">
        <el-form-item label="">
          <el-select v-model="form.encryptType" placeholder="" @change="handleEncrypt">
            <el-option label="Base64" value="base64"></el-option>
            <el-option label="URL" value="url"></el-option>
            <el-option label="MD5" value="md5"></el-option>
            <el-option label="SHA1" value="sha1"></el-option>
            <el-option label="SHA256" value="sha256"></el-option>
            <el-option label="HTML" value="html"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input
            type="textarea"
            v-model="form.originalText"
            placeholder=""
            :rows="6"
            @input="handleEncrypt"
          ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input
            type="textarea"
            v-model="form.encryptedText"
            placeholder=""
            :rows="6"
            readonly
          ></el-input>
          <div class="button-group">
            <el-button type="primary" @click="copyToClipboard(form.encryptedText)">
              <i class="el-icon-copy-document"></i> 
            </el-button>
<!--            <el-button @click="swapTexts">-->
<!--              <i class="el-icon-refresh"></i> -->
<!--            </el-button>-->
          </div>
        </el-form-item>

        <el-form-item label="" v-if="canDecrypt">
          <el-input
            type="textarea"
            v-model="form.decryptedText"
            placeholder=""
            :rows="4"
            readonly
          ></el-input>
          <el-button type="success" @click="copyToClipboard(form.decryptedText)">
            <i class="el-icon-copy-document"></i> 
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <el-card class="info-card">
      <div slot="header">
        <span></span>
      </div>
      <el-collapse>
        <el-collapse-item title="Base64" name="base64">
          <p>Base6464HTTP</p>
        </el-collapse-item>
        <el-collapse-item title="URL" name="url">
          <p>URL%</p>
        </el-collapse-item>
        <el-collapse-item title="MD5" name="md5">
          <p>MD5128</p>
        </el-collapse-item>
        <el-collapse-item title="SHA" name="sha">
          <p>SHASHA1160SHA256256</p>
        </el-collapse-item>
      </el-collapse>
    </el-card>
  </div>
</template>

<script>
import CryptoJS from 'crypto-js'
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'EncryptView',
  metaInfo: {
    title: ' - ',
    meta: [
      { name: 'description', content: 'Base64URLMD5SHA1SHA256' },
      { name: 'keywords', content: ',,Base64,MD5,SHA256,URL,' }
    ]
  },
  data() {
    return {
      form: {
        encryptType: 'base64',
        originalText: '',
        encryptedText: '',
        decryptedText: ''
      }
    }
  },
  computed: {
    canDecrypt() {
      return ['base64', 'url', 'html'].includes(this.form.encryptType)
    }
  },
  methods: {
    handleEncrypt() {
      if (!this.form.originalText) {
        this.form.encryptedText = ''
        this.form.decryptedText = ''
        return
      }

      try {
        switch (this.form.encryptType) {
          case 'base64':
            this.form.encryptedText = btoa(unescape(encodeURIComponent(this.form.originalText)))
            this.form.decryptedText = decodeURIComponent(escape(atob(this.form.encryptedText)))
            break
          case 'url':
            this.form.encryptedText = encodeURIComponent(this.form.originalText)
            this.form.decryptedText = decodeURIComponent(this.form.encryptedText)
            break
          case 'md5':
            this.form.encryptedText = CryptoJS.MD5(this.form.originalText).toString()
            break
          case 'sha1':
            this.form.encryptedText = CryptoJS.SHA1(this.form.originalText).toString()
            break
          case 'sha256':
            this.form.encryptedText = CryptoJS.SHA256(this.form.originalText).toString()
            break
          case 'html':
            this.form.encryptedText = this.form.originalText
              .replace(/&/g, '&amp;')
              .replace(/</g, '&lt;')
              .replace(/>/g, '&gt;')
              .replace(/"/g, '&quot;')
              .replace(/'/g, '&#39;')
            this.form.decryptedText = this.form.encryptedText
              .replace(/&amp;/g, '&')
              .replace(/&lt;/g, '<')
              .replace(/&gt;/g, '>')
              .replace(/&quot;/g, '"')
              .replace(/&#39;/g, "'")
            break
        }
      } catch (error) {
        this.$message.error('' + error.message)
      }
    },

    swapTexts() {
      if (this.canDecrypt && this.form.decryptedText) {
        const temp = this.form.originalText
        this.form.originalText = this.form.decryptedText
        this.form.decryptedText = temp
        this.handleEncrypt()
        console.log(171)
      } else {
        const temp = this.form.originalText
        this.form.originalText = this.form.encryptedText
        this.form.encryptedText = temp
        this.handleEncrypt()
        console.log(177)
      }
      console.log(this.form.originalText)
      console.log(this.form.decryptedText)
      this.$forceUpdate()
    },

    async copyToClipboard(text) {
      if (!text) {
        this.$message.warning('')
        return
      }

      try {
        const success = await ClipboardUtil.copyText(text)
        if (success) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
      } catch (error) {
        this.$message.error(': ' + error.message)
      }
    }
  }
}
</script>

